import request from '@/utils/request.js'
import qs from 'qs'

export const getHistoryDataApi = params => {
    return request({
        url: '/v2/users/history.php',
        params
    })
    .then(res => res.data)
}
export const getUsersListApi = params => {
    return request({
        url: '/v2/users/index.php',
        params
    })
    .then(res => res.data)
}
export const createUserApi = params => {
    return request({
        url: '/v2/users/create.php',
        method: 'post',
        data: qs.stringify(params)
    })
    .then(res => res.data)
}

export const changeRoleApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/users/assign.php
        url: '/v2/users/assign.php',
        method: 'put',
        data: qs.stringify(params)
    })
    .then(res => res.data)
}