import request from '@/utils/request.js'
import qs from 'qs'

export const getRolesDataApi = () => {
    return request({
        // http://kg.zhaodashen.cn/v2/roles/index.php
        url: '/v2/roles/index.php',
    })
    .then(res => res.data)
}

export const createRoleApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/roles/create.php
        url: '/v2/roles/create.php',
        method: 'post',
        data: qs.stringify(params)
    })
    .then(res => res.data)
}

export const delRoleApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/roles/delete.php
        url: '/v2/roles/delete.php',
        method: 'delete',
        params
    })
    .then(res => res.data)
}
export const changeAuthsApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/roles/assign.php
        url: '/v2/roles/assign.php',
        method: 'put',
        data: qs.stringify(params)
    })
    .then(res => res.data)
}
