<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix header">
            <span>用户列表</span>
            <el-button type="primary" @click="$router.push('/users/create')" round>创建</el-button>
        </div>
        <div class="filter">
            <el-input placeholder="请输入内容" class="input-with-select" v-model="searchVal">
                <el-button slot="append" icon="el-icon-search" @click="searchHandler"></el-button>
            </el-input>

            <el-date-picker
            type="daterange"
            v-model="dateVal"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
            </el-date-picker>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column label="编号" prop="user_id"></el-table-column>
            <el-table-column label="所属角色" prop="role_name"></el-table-column>
            <el-table-column label="用户名" prop="username"></el-table-column>
            <el-table-column label="手机号" prop="mobile_phone"></el-table-column>
            <el-table-column label="冻结" prop="state">
                <template slot-scope="item">
                    <el-switch
                    v-model="item.row.state"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    @change="stateHandler(item.row)">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column label="日期" prop="reg_time"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="item">
                    <el-button type="primary" icon="el-icon-user" @click="changeHandler(item.row)" circle></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="delHandler(item.$index)" circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        
        <!-- 修改用户 -->
        <el-dialog
            title="角色分配"
            :visible.sync="isShow"
            width="30%"
            :before-close="handleClose">
                <el-form label-width="80px">
                    <el-form-item label="当前帐号">
                        <el-input v-model="form.username" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="当前角色">
                        <el-input v-model="form.role_name" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="选择角色">
                        <el-select v-model="form.role_id">
                            <!-- <el-option label="区域一" value="shanghai"></el-option> -->
                            <el-option v-for="(item, index) in rolesList" :key="index" :label="item.role_name" :value="item.role_id">

                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="isShow = false">取 消</el-button>
                <el-button type="primary" @click="onSubmit">确 定</el-button>
            </span>
        </el-dialog>

        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total">
        </el-pagination>
    </el-card>
</template>
<script>
import {getUsersListApi, changeRoleApi} from '@/api/users.js'
import {getRolesDataApi} from '@/api/roles.js'
export default {
    data() {
        return {
            form: {
                username: '',
                role_name: '',
                user_id: '',
                role_id: '',
            },
            pickerOptions: {
              shortcuts: [{
                text: '最近一周',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近一个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  picker.$emit('pick', [start, end]);
                }
              }, {
                text: '最近三个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  picker.$emit('pick', [start, end]);
                }
              }]
            },
            isShow: false,
            searchVal: '',
            dateVal: '',
            pagination: {
                total: 0,
                currentPage: 1,
                pageSize: 5,
            },
            tableData: [],
            rolesList: []
        }
    },
    created() {
        getUsersListApi()
        .then(res => {
            console.log(res);
            this.tableData = res.data.list
            this.pagination.total = parseInt(res.data.total)
            this.tableData.forEach(item => {
                item.state == 0 ? item.state = false : item.state = true
            })
        })
    },
    methods: {
        handleSizeChange(val) {
            // console.log(`每页 ${val} 条`);
            this.pagination.pageSize = val
            getUsersListApi({
                pagenum: this.pagination.currentPage,
                pagesize: val
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
                this.tableData.forEach(item => {
                    item.state == 0 ? item.state = false : item.state = true
                })
            })
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
            this.pagination.currentPage = val
            getUsersListApi({
                pagenum: val,
                pagesize: this.pagination.pageSize
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
                this.tableData.forEach(item => {
                    item.state == 0 ? item.state = false : item.state = true
                })
            })
        },
        delHandler(index) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$axios.delete(`/api/v2/users/delete.php?user_id=${this.tableData[index].user_id}`)
                this.tableData.splice(index, 1)
                this.$message.success('删除成功!');
                getUsersListApi()
                .then(res => {
                    console.log(res);
                    this.tableData = res.data.list
                    this.pagination.total = parseInt(res.data.total)
                    this.tableData.forEach(item => {
                        item.state == 0 ? item.state = false : item.state = true
                    })
                })
            }).catch(() => {
                this.$message.info('已取消删除');          
            });
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
        changeHandler(item) {
            this.isShow = true
            this.form.username = item.username
            this.form.role_name = item.role_name
            this.form.user_id = item.user_id
            getRolesDataApi().then(res => {
                this.rolesList = res.data
                // console.log(this.rolesList);
            })
        },
        stateHandler(item) {
            // console.log(item);
            // http://kg.zhaodashen.cn/v2/users/state.php
            this.$axios({
                url: `/api/v2/users/state.php`,
                method: 'put',
                headers: {
                    token: localStorage.getItem('token')
                },
                data: `user_id=${parseInt(item.user_id)}&state=${item.state ? 1 : 0}`
            })
        },
        searchHandler() {
            getUsersListApi({
                uname: this.searchVal
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        },
        onSubmit() {
            changeRoleApi({
                user_id: this.form.user_id,
                role_id: this.form.role_id
            })
            .then(res => {
                if(res.meta.state === 200) {
                    this.$message.success(res.meta.msg)
                    getUsersListApi()
                    .then(res => {
                        console.log(res);
                        this.tableData = res.data.list
                        this.pagination.total = parseInt(res.data.total)
                        this.tableData.forEach(item => {
                            item.state == 0 ? item.state = false : item.state = true
                        })
                    })
                }else {
                    this.$message.error(res.meta.msg)
                }
            })
            this.isShow = false
        }
    },
    watch: {
        dateVal(newData, oldData) {
            // console.log(newData);
            getUsersListApi({
                start_time: newData[0],
                end_time: newData[1]
            })
            .then(res => {
                this.tableData = res.data.list
                this.pagination.total = parseInt(res.data.total)
            })
        }
    }
}
</script>
<style scoped>
    .el-card .filter {
        margin-bottom: 20px;
        overflow: hidden
    }
    .el-card .filter .el-input {
        width: 300px;
        float: left
    } 
    .el-card .filter .el-date-editor {
        float: right;
    }
    .el-pagination {
        width: 100%;
        padding: 10px 0;
        background: #ebeef5;
        text-align: right;
        margin-top: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
    .el-card .header .el-button {
        float: right;
        margin-top: -10px
    }
</style>